* {
    margin:0;
    padding:0;
    border: 0;
    box-sizing: border-box;
}

html,
body {
    height: 100%;
    font-family: "Open Sans"
}

html { font-size: 16px; }

ul {
    list-style-type: none;
}
@media screen and (min-width: 25em){
    font-size: -webkit-calc(112.5% + 4 * (100vw - 768px) / 512);
    font-size: calc(112.5% + 4 * (100vw - 768px) / 512);
  }


.welcome {
    min-height: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: radial-gradient(ellipse, $color-normal, $color-golden-grass);

    h1 {
        color: $color-white;
        font-weight: bold;
        font-size: 3.8125rem;
        margin: 1rem;
    }

    .welcome-description {
        line-height: 1.5;
        padding: 0 0.5rem;
        font-size: 2rem;
        padding: 2rem;
    }
    a.scroll-down {
        color: $color-white;
        text-decoration: none;
        font-size: 1rem;
        border: 0.1rem solid $color-white;
        padding: 1rem;
        border-radius: 0.3rem;
        margin: 1rem;
        position: relative;
        background-color: $color-dark;
        padding-right: 1.3rem;
        display: flex;
        justify-content: center;
        align-items: center;
        &:hover {
            border: 0.1rem solid $color-normal;
        }
        .continue-arrow {
            margin: 0 0.2rem;
            &:after {
                content: "";
                display: block;
                width: 0px;
                height: 0px;
                border-top: 0.4rem solid $color-white;
                border-left: 0.4rem solid transparent;
                border-right: 0.4rem solid transparent;
            }
            
        }
    }
}

.top {
    display: block;
    text-align: right;
}

.top-arrow {
    margin: 0 0.2rem;
    position: relative;
    &:after {
        position: absolute;
        top: 0.5rem;
        content: "";
        width: 0px;
        height: 0px;
        border-bottom: 0.4rem solid $color-dark;
        border-left: 0.4rem solid transparent;
        border-right: 0.4rem solid transparent;
    }
    
}

a {
    color: $color-dark;
}

p {
    line-height: 1.3;
}

.container {
    ul {
        list-style-type: disc;
    }
    ol, ul {
    padding:1rem 2rem;
    li {
        line-height: 1.2;
        margin-bottom: 1rem;
        }
    }
}

.content {
    padding: 1.5rem;

    h2 {
        font-size: 3rem;
        color: $color-dark;
        font-weight: bold;
        padding: 1rem 0;
        line-height: 1.3;
    }

    blockquote {
        border-left: 0.2rem solid $color-athens-gray;
        padding-left: 1rem;
        margin: 1rem 0;
    }

    .blockquote-footer {
        margin: 0.5rem 0;
    }
    
    
    
}